import { motion } from "framer-motion";
import { Link } from "react-router-dom";

export default function SimilarProducts({ id, image, name, price }) {
  const formattedName = encodeURIComponent(name).replace(/%20/g, "-");

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.4 }}
    >
      <Link
        to={`/products/${formattedName}`}
        className="group block bg-gradient-to-b from-white via-gray-50 to-white border border-gray-100 rounded-2xl shadow-md hover:shadow-xl transition-all duration-300 p-4"
      >
        <div className="overflow-hidden rounded-xl mb-3">
          <img
            src={image}
            alt={name}
            loading="lazy"
            className="w-full h-40 object-cover rounded-xl group-hover:scale-105 transition-transform duration-300"
          />
        </div>
        <h3 className="text-lg font-semibold text-gray-800 group-hover:text-blue-600 transition-colors truncate">
          {name}
        </h3>
        <p className="text-blue-600 text-sm font-bold mt-1">
          {Number(price).toLocaleString("fa-IR")} تومان
        </p>
      </Link>
    </motion.div>
  );
}
