import { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { PackageCheck, Palette, Weight, ImagePlus } from "lucide-react";

export default function ProductDetailsSection({ product, onAddToCart }) {
  const [selectedImage, setSelectedImage] = useState(
    product?.images?.[0] || null
  );
  const [slideDirection, setSlideDirection] = useState(0);
  const [isPending, setIsPending] = useState(false);
  const isAvailable = product?.stock > 0;
  const imageContainerRef = useRef(null);

  // Preload images for smooth transitions
  useEffect(() => {
    if (product?.images && product.images.length > 0) {
      const preloadImages = () => {
        product.images.forEach((src) => {
          const img = new Image();
          img.src = src;
        });
      };
      preloadImages();
    }
  }, [product?.images]);

  // Function to handle image change with direction tracking
  const handleImageChange = (newImage, index) => {
    if (isPending || selectedImage === newImage) return;

    // Find current image index
    const currentIndex =
      product?.images?.findIndex((img) => img === selectedImage) || 0;

    // Set direction and update image
    setIsPending(true);
    setSlideDirection(index > currentIndex ? -1 : 1);
    setSelectedImage(newImage);

    // Reset pending state after animation completes
    setTimeout(() => setIsPending(false), 300);
  };

  // Reset images when product changes
  useEffect(() => {
    if (product?.images?.[0]) {
      setSelectedImage(product.images[0]);
      setSlideDirection(0);
    }
  }, [product?.id]);

  // Animation variants for super smooth transitions
  const variants = {
    enter: (direction) => ({
      x: direction * 500,
      opacity: 1,
    }),
    center: {
      x: 0,
      opacity: 1,
    },
    exit: (direction) => ({
      x: direction * -500,
      opacity: 1,
      position: "absolute",
    }),
  };

  return (
    <section className="grid md:grid-cols-2 gap-10 mb-10">
      {/* Product Gallery with ultra-smooth animations */}
      <div>
        <figure
          className="relative overflow-hidden rounded-xl shadow-md h-[400px] bg-gray-50"
          ref={imageContainerRef}
        >
          <AnimatePresence
            initial={false}
            custom={slideDirection}
            mode="popLayout"
          >
            <motion.img
              key={selectedImage}
              src={selectedImage}
              alt={`${product?.name}`}
              className="w-full h-full object-cover absolute top-0 left-0"
              variants={variants}
              initial="enter"
              animate="center"
              exit="exit"
              custom={slideDirection}
              transition={{
                type: "spring",
                stiffness: 300,
                damping: 30,
                mass: 1,
              }}
              loading={
                selectedImage === product?.images?.[0] ? "eager" : "lazy"
              }
              fetchpriority={
                selectedImage === product?.images?.[0] ? "high" : "auto"
              }
              width="600"
              height="400"
            />
          </AnimatePresence>
          <figcaption className="sr-only">{product?.name}</figcaption>
        </figure>

        {product?.images?.length > 1 && (
          <div className="mt-4">
            <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide">
              {product?.images.map((img, i) => (
                <button
                  key={i}
                  onClick={() => handleImageChange(img, i)}
                  className={`flex-shrink-0 w-16 h-16 rounded-lg border-2 transition-all duration-200 ${
                    selectedImage === img
                      ? "border-blue-500 scale-105 shadow-md"
                      : "border-gray-200 hover:border-blue-300"
                  }`}
                  disabled={isPending}
                  aria-label={`نمایش تصویر ${i + 1} از ${product.name}`}
                >
                  <img
                    src={img}
                    alt={`${product.name} - تصویر ${i + 1}`}
                    className="object-cover w-full h-full rounded-lg"
                    loading="lazy"
                    width="64"
                    height="64"
                  />
                </button>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Product Info */}
      <div className="space-y-4">
        <h1 className="text-2xl font-bold text-gray-800">{product?.name}</h1>

        <div className="flex items-center gap-2 text-blue-600 font-semibold text-xl">
          <span>{Number(product?.price)?.toLocaleString("fa-IR")} تومان</span>
        </div>

        <ul className="text-gray-700 space-y-2 text-sm">
          {product?.material && (
            <li className="flex items-center gap-2">
              <PackageCheck className="w-4 h-4 text-gray-500" />
              <span>جنس: {product.material}</span>
            </li>
          )}
          {product?.brand && (
            <li className="flex items-center gap-2">
              <Palette className="w-4 h-4 text-gray-500" />
              <span>برند: {product.brand.toUpperCase()}</span>
            </li>
          )}
          {product?.weight_capacity && (
            <li className="flex items-center gap-2">
              <Weight className="w-4 h-4 text-gray-500" />
              <span>تحمل وزن: {product.weight_capacity}kg</span>
            </li>
          )}
          <li className="flex items-center gap-2">
            <ImagePlus className="w-4 h-4 text-gray-500" />
            <span>وضعیت: {isAvailable ? "موجود" : "ناموجود"}</span>
          </li>
        </ul>
        {/* add To cart button  */}
        {/* <button
          className="mt-6 bg-blue-600 text-white py-3 px-6 rounded-xl hover:bg-blue-700 transition text-base font-medium"
          disabled={!isAvailable}
          onClick={() => onAddToCart(product.id)}
          aria-label={isAvailable ? "افزودن به سبد خرید" : "محصول ناموجود است"}
        >
          افزودن به سبد خرید
        </button> */}
        {/* className="mt-6 bg-blue-600 text-white py-3 px-6 rounded-xl hover:bg-blue-700 transition text-base font-medium" */}
        <a
          href="tel:09392566494"
          aria-label={isAvailable ? "تماس بگیرید" : "محصول ناموجود است"}
        >
          <button
            className="mt-6 bg-blue-600 text-white py-3 px-6 rounded-xl hover:bg-blue-700 transition text-base font-medium"
            disabled={!isAvailable}
            onClick={() => onAddToCart(product.id)}
            aria-label={isAvailable ? "تماس بگیرید" : "محصول ناموجود است"}
          >
            تماس بگیرید
          </button>
        </a>
      </div>
    </section>
  );
}
