import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import Header from "../ComponentChilds/Header";
import Footer from "../ComponentChilds/Footer";
import ProductInfoTabs from "../ComponentChilds/products/ProductsInfoTabs";
import ProductDetailsSection from "../ComponentChilds/products/productDetailSection";
import SimilarProducts from "../ComponentChilds/products/SimilarProducts";
import Loader from "../ComponentChilds/Loader";
import SimilarProductLoader from "../ComponentChilds/products/SimilarProductLoader";
import { Helmet } from "react-helmet"; // Add for better SEO management

const ProductDetail = ({ basketItemCount, onSetBasketItemCount }) => {
  const { productName } = useParams();
  const [product, setProduct] = useState(null); // Initialize as null, not an empty array
  const [similarProducts, setSimilarProducts] = useState([]); // For similar products
  const [loading, setLoading] = useState(true); // Add loading state
  const [error, setError] = useState(null); // Add error handling
  const [loadingSimilar, setLoadingSimilar] = useState(false);

  const slugify = (text) =>
    text.toLowerCase().replace(/\s+/g, "-").replace(/-+/g, "-");

  useEffect(() => {
    if (!productName) return;

    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/products/"
        : "https://allmaxsport.com/api/products/";

    const loadProductDetail = async () => {
      try {
        setLoading(true);
        const response = await fetch(API_URL);

        if (!response.ok) {
          throw new Error(`Failed to fetch product: ${response.status}`);
        }

        const data = await response.json();

        // Create a slug from product names to compare
        const matchedProduct = data.find(
          (p) => slugify(p.name) === decodeURIComponent(productName)
        );

        if (!matchedProduct) {
          throw new Error("Product not found.");
        }

        setProduct(matchedProduct);

        // Load similar products (from same category)
        if (matchedProduct.category) {
          loadSimilarProducts(data, matchedProduct.id);
        }
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    const loadSimilarProducts = async (productsData, currentProductId) => {
      try {
        setLoadingSimilar(true);
        const filtered = productsData
          .filter((item) => item.id !== currentProductId)
          .slice(0, 4);

        setSimilarProducts(filtered);
      } catch (err) {
      } finally {
        setLoadingSimilar(false);
      }
    };

    loadProductDetail();
  }, [productName]);

  function handleAddToCart() {
    const shoppingBasket =
      JSON.parse(localStorage.getItem("shoppingBasket")) || [];
    const existingProduct = shoppingBasket.find(
      (item) => item.ProductId === product.id
    );

    if (existingProduct) {
      // If already in cart, increase quantity
      existingProduct.quantity += 1;
    } else {
      // If not in cart, add it
      shoppingBasket.push({
        ProductId: product.id,
        id: shoppingBasket.length + 1,
        productName: product.name,
        quantity: 1,
        price: Number(product.price),
        imageUrl: product.images[0],
        description: product.description,
      });
    }

    localStorage.setItem("shoppingBasket", JSON.stringify(shoppingBasket));
    onSetBasketItemCount(shoppingBasket.length);
  }

  // Show loader while fetching data
  if (loading) {
    return (
      <>
        <Header basketItemCount={basketItemCount} />
        <div className="flex justify-center items-center min-h-screen">
          <Loader color="black">لطفا صبر کنید...</Loader>
        </div>
        <Footer />
      </>
    );
  }

  // Show error message if fetch failed
  if (error) {
    return (
      <>
        <Header basketItemCount={basketItemCount} />
        <div className="flex justify-center items-center min-h-screen">
          <div className="bg-red-100 p-4 rounded-md text-red-700 text-center max-w-lg">
            <h2 className="text-xl font-bold mb-2">خطا در بارگذاری محصول</h2>
            <p>{error}</p>
            <button
              className="mt-4 bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
              onClick={() => window.location.reload()}
            >
              تلاش مجدد
            </button>
          </div>
        </div>
        <Footer />
      </>
    );
  }

  // If no product found after loading
  if (!product) {
    return (
      <>
        <Header basketItemCount={basketItemCount} />
        <div className="flex justify-center items-center min-h-screen">
          <div className="text-center p-4">
            <h2 className="text-xl font-bold mb-2">محصول یافت نشد</h2>
            <p>متأسفانه محصول مورد نظر شما در سیستم وجود ندارد.</p>
          </div>
        </div>
        <Footer />
      </>
    );
  }

  return (
    <>
      {/* Enhanced SEO with Helmet */}
      <Helmet>
        <title>{`${product.name} | آلمکس`}</title>
        <meta
          name="description"
          content={product.description?.substring(0, 155)}
        />
        <meta
          name="keywords"
          content={`${product.category}, ${product.name}, المکس, تجهیزات ورزشی, وسایل باشگاه نقد و اقساط`}
        />
        <meta property="og:title" content={`${product.name} | المکس`} />
        <meta
          property="og:description"
          content={product?.description?.substring(0, 155)}
        />
        {/* && product?.images[0]  */}
        {product.images && (
          <meta property="og:image" content={product?.images} />
        )}
        <meta property="og:type" content="product" />
        <link
          rel="canonical"
          href={`https://allmaxsport.com/products/${[productName]}`}
        />
      </Helmet>

      <Header basketItemCount={basketItemCount} />

      {/* Schema.org markup for SEO */}
      <script type="application/ld+json">
        {JSON.stringify({
          "@context": "https://schema.org",
          "@type": "Product",
          name: product.name,
          image: product.images,
          description: product.description,
          brand: {
            "@type": "Brand",
            name: "آلمکس - Allmax",
          },
          offers: {
            "@type": "Offer",
            priceCurrency: "IRR",
            price: product.salePrice || product.price,
            availability:
              product.stock > 0
                ? "https://schema.org/InStock"
                : "https://schema.org/OutOfStock",
            itemCondition: "https://schema.org/NewCondition",
          },
          ...(product.rating && {
            aggregateRating: {
              "@type": "AggregateRating",
              ratingValue: product.rating,
              bestRating: "5",
              worstRating: "1",
              ratingCount: "1",
            },
          }),
        })}
      </script>
      <main className="bg-white text-right font-vazir px-4 sm:px-6 max-w-8xl mx-auto py-10">
        {/* Product Info + Gallery */}
        {loading ? (
          <Loader color="text-blue-500" dir="row" textSize="text-xl" children>
            درحال بارگذاری محصول
          </Loader>
        ) : (
          <>
            <section className="max-w-6xl mx-auto">
              <ProductDetailsSection
                product={product}
                onAddToCart={handleAddToCart}
              />
            </section>

            {/* Tabs for Features/Description */}
            <section className="max-w-6xl mx-auto">
              <ProductInfoTabs
                features={product.features}
                description={product.description}
              />
            </section>
          </>
        )}

        {/* Similar Products */}
        {similarProducts.length > 0 || loadingSimilar ? (
          <section className="px-6 md:px-12 lg:px-24 py-10 text-center">
            {/* Fancy header with lines */}
            <div className="relative flex items-center justify-center mb-10">
              <div className="w-full h-px bg-gray-200"></div>
              <h2 className="px-4 text-2xl font-bold text-gray-700 whitespace-nowrap">
                محصولات مشابه
              </h2>
              <div className="w-full h-px bg-gray-200"></div>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
              {loadingSimilar
                ? [...Array(4)].map((_, i) => <SimilarProductLoader key={i} />)
                : similarProducts.map((p) => (
                    <SimilarProducts
                      key={p?.id}
                      id={p?.id}
                      image={p?.images?.[0]}
                      name={p?.name}
                      price={p?.price}
                    />
                  ))}
            </div>
          </section>
        ) : null}
      </main>

      <Footer />
    </>
  );
};

export default ProductDetail;
